import { Region, TextRegion } from 'pages/Daily/components/Region';
import { useState } from 'react';
// Ant Design Resources
import { BarChartOutlined } from '@ant-design/icons';
import { Button, Divider, Flex, Layout, Modal, Space, Typography } from 'antd';
// Types
import type { Me } from 'types/user';
// Icons
import { DailyWordGameIcon } from 'icons/DailyWordGameIcon';
// Components
import { DualTranslate, Translate } from 'components/language';
// Internal
import { getInitialState } from '../utils/helpers';
import { SETTINGS } from '../utils/settings';
import type { DailyPalavreadoEntry } from '../utils/types';
import { usePalavreadoEngine } from '../utils/usePalavreadoEngine';
import { Header } from '../../../components/Header';
import { Menu } from '../../../components/Menu';
import { Board } from './Board';
import { ResultsModalContent } from './ResultsModalContent';
import { Rules } from './Rules';

type DailyPalavreadoProps = {
  data: DailyPalavreadoEntry;
  currentUser: Me;
};

export function DailyPalavreado({ data }: DailyPalavreadoProps) {
  const [initialState] = useState(getInitialState(data));
  const {
    hearts,
    selection,
    guesses,
    letters,
    showResultModal,
    setShowResultModal,
    isWin,
    isComplete,
    selectLetter,
    submitGrid,
    swap,
    swaps,
    size,
    keyword,
  } = usePalavreadoEngine(data, initialState);

  return (
    <Layout className="app">
      <Header icon={<DailyWordGameIcon />} localStorageKey={SETTINGS.KEY}>
        TD <DualTranslate>{SETTINGS.NAME}</DualTranslate> #{data.number}
      </Header>
      <Layout.Content>
        <Menu hearts={hearts} total={Math.max(SETTINGS.HEARTS, size)} openRules={true} rules={<Rules />} />

        <Region>
          <Typography.Text strong className="palavreado-word">
            {keyword} {swaps > 0 && `  ${swaps}`}
          </Typography.Text>
          <Board
            letters={letters}
            onLetterSelection={selectLetter}
            selection={selection}
            swap={swap}
            guesses={guesses}
            size={size}
          />
        </Region>

        <Region>
          {isComplete ? (
            <Button onClick={() => setShowResultModal(true)} type="primary" icon={<BarChartOutlined />}>
              <Translate pt="Ver Resultado" en="Show Results" />
            </Button>
          ) : (
            <Button type="primary" onClick={submitGrid} disabled={isComplete} block>
              <Translate pt="Enviar" en="Submit" />
            </Button>
          )}
        </Region>

        <TextRegion direction="vertical" size="small">
          {guesses.map((attempt, index) => (
            <Space
              key={`${attempt}-${index}`}
              split={<Divider type="vertical" className="mx-0" />}
              size="small"
            >
              {attempt.map((word, i) => (
                <Typography.Text
                  keyboard
                  className="palavreado-word"
                  key={`${attempt}-${index}-${word}-${i}`}
                >
                  {word}
                </Typography.Text>
              ))}
            </Space>
          ))}
        </TextRegion>

        {guesses.length > 0 && (
          <TextRegion>
            <Flex className="contained" gap={12} align="center">
              <div className="palavreado-board__tile palavreado-board__tile--place-guessed palavreado-board__tile--sample">
                ?
              </div>
              <Typography.Text>
                <Translate
                  pt={
                    <>
                      Posies que j foram testadas com a letra especfica aparecem assim (borda preta
                      pontilhada).
                      <br />
                      Evite enviar com letras assim, elas continuaro erradas.
                    </>
                  }
                  en={
                    <>
                      Positions that have already been tested with a specific letter appear like this (dotted
                      black border).
                      <br />
                      Avoid submitting with letters like this, they will remain wrong.
                    </>
                  }
                />
              </Typography.Text>
            </Flex>
          </TextRegion>
        )}

        <Modal
          title={<Translate pt="Resultado" en="Results" />}
          open={showResultModal}
          onCancel={() => setShowResultModal(false)}
          footer={null}
        >
          <ResultsModalContent
            challenge={data?.number}
            isWin={isWin}
            hearts={hearts}
            words={data.words}
            letters={letters}
            swaps={swaps}
            size={size}
          />
        </Modal>
      </Layout.Content>
    </Layout>
  );
}
